	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Borders are a Pain","date":"03 Sep 2001","slug":"/2001-09-03-borders-are-a-pain/","tags":"Y2001"},"html":"<p>Christopher's tricks now include ringing the doorbell (repeatedly), flushing the toilet (repeatedly), smearing cat food on the door handles, dragging hair dryers and irons along the carpet, and singing loudly to himself. He is very amused if you sing back to him. </p>\n<p>Borders are a pain, even in this modern day and age. Last week I tried to buy plane tickets to Minneapolis for this winter. Now, given that most German travel agents have no idea where Minneapolis is, never mind which airlines fly there, it's quicker for me to find tickets online myself. I know that to get there with one stop, the only airline I can take is <a href=\"http://www.nwa.com\">Northwest</a> (otherwise known as the \"Aeroflot of the Midwest\"). And at the Northwest website, I can find the flight I want. But can I buy my ticket there? No, not from Germany. </p>\n<p>Or what about sending money to and from the States? Credit cards are nice for buying things, and when we are in the States we can withdraw funds with our German ATM card. But what if my parents want to send money for Christopher, or what if Mama's brother Roland wants to sell something to an American on E-Bay? Wire transfers are expensive, and not always reliable (once when wiring money to Minnesota, it got hung up in St. Louis for 6 months). And sending currency through the mail? I don't think so. Well, now there's <a href=\"http://www.paypal.com\">PayPal</a>, and they now take international customers, and will even send funds to a German bank account. But only if you have a credit card. Roland doesn't have one, so I guess he's out of luck.</p>"},"next":{"fields":{"title":"Layover at Home","date":"02 Sep 2001","slug":"/2001-09-02-layover-at-home/"}},"previous":{"fields":{"title":"No Broadband","date":"04 Sep 2001","slug":"/2001-09-04-no-broadband/"}}},{"node":{"fields":{"title":"Layover at Home","date":"02 Sep 2001","slug":"/2001-09-02-layover-at-home/","tags":"Y2001"},"html":"<p>Mama's back from Verona, and we're all three back from the boat party in Kiel. Mama's going to demonstrate her newly learned pasta-rolling skills tonight.</p>\n<p><a href=\"http://andrea.editthispage.com/2001/09/01\">Andrea</a> points to National Geographic's <a href=\"http://www.nationalgeographic.com/traveler/index50.html\">50 Places of a Lifetime</a>. The list looks like enough for a dozen lifetimes to me. Funny things is, in a bi-national family we have <i>less</i> opportunity to see places than we would otherwise, since our resources we have for travelling (mainly time, but also money) are spent visiting family and relatives. My parents have seen more of Europe than I have, and Mama's brothers have seen more of the US than Mama has. So it goes. But at least I have one place on both <a href=\"http://andrea.editthispage.com\">Andrea</a> and <a href=\"http://www.2020hindsight.org/\">Susan</a>, namely <a href=\"http://www.ipanema.com/\">Rio de Janeiro</a>. And Mama has been to <a href=\"http://www.istanbulcityguide.com/\">Istanbul</a>.</p>"},"next":{"fields":{"title":"Roll Your Own","date":"30 Aug 2001","slug":"/2001-08-30-roll-your-own/"}},"previous":{"fields":{"title":"Borders are a Pain","date":"03 Sep 2001","slug":"/2001-09-03-borders-are-a-pain/"}}},{"node":{"fields":{"title":"Roll Your Own","date":"30 Aug 2001","slug":"/2001-08-30-roll-your-own/","tags":"Y2001"},"html":"<p>Mama checked in from Verona this evening (via hotel phone, so we kept it short). She was at a do-it-yourself pasta restaurant this afternoon. Do-it-yourself as in here's an egg, 100 g flour, and a rolling pin. Roll it yourself. I had to think of <a href=\"http://www.vfth.com\">Al</a> as she was telling me this. So maybe now she'll know what to do with that pasta machine we've had sitting around for years.</p>"},"next":{"fields":{"title":"Verona","date":"29 Aug 2001","slug":"/2001-08-29-verona/"}},"previous":{"fields":{"title":"Layover at Home","date":"02 Sep 2001","slug":"/2001-09-02-layover-at-home/"}}}],"pathPrefix":"","first":false,"last":false,"index":979,"pageCount":1101}}

